.main {
  background: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(./다운로드.jpg), center no-repeat;
  background-size: cover;
  width: 100%;
  height: 600px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

.load {
  display: block;
  margin: auto;
}

.alert {
  width: 100%;
  height: 70px;
  background-color: cornflowerblue;
  margin-top: 50px;
  border-radius: 5px;
  color: white;
  font-size: 25px;
}

.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: all 500ms;
}

.side-cart {
  width: 150px;
  height: 350px;
  position: fixed;
  right: 0;
  top: 50%;
  background: red;
  padding: 10px;
  transition: 0.5s;
  img {
    margin: 5px auto;
  }
  p {
    color: white;
  }
}

.side-btn {
  position: absolute;
  display: inline-block;
  left: -30px;
  background: red;
  border-radius: 5px 0 0 5px;
  width: 30px;
  height: 30px;
  top: 0;
}

.hide {
  transform: translateX(150px);
  transition: 0.5s;
}
